const filter = {
  school: ['SW ', 'SW ', 'SW '],
  major: ['SW', 'SW', ''],
  info: ['', '', '', '', '', '', ' ( )'],
  language: ['Java', 'C#', 'Node.js', 'Python', 'Django', 'C / C++', 'Dart', 'Kotlin', 'Go'],
  field: ['', ' ', ' ', ' ', ' (,  )', '', ''],
  company: ['', '', '', '', '', '', '', '', '', '', '/LG/SK  '],
}

const filterMajor = (datas) => {
  let filterdDatas = [...datas]
  let count = 0

  filterdDatas = filterdDatas.filter((data) => {
    let checked = false
    filter.major.map((check) => {
      if (data.name === check) {
        checked = true
      }
    })
    if (!checked) {
      count += data.count
    }
    return checked
  })

  if (count !== 0) {
    filterdDatas = [...filterdDatas, { name: '', count }]
  }

  return filterdDatas
}

const filterInfo = (datas) => {
  let filterdDatas = [...datas]
  let count = 0

  filterdDatas = filterdDatas.filter((data) => {
    let checked = false
    filter.info.map((check) => {
      if (data.name === check) {
        if (data.name.indexOf('( )')) {
          data.name = data.name.replace('( )', '')
        }
        if (data.name.indexOf('') !== -1) {
          data.name = ' '
        }
        checked = true
      }
    })
    if (!checked) {
      count += data.count
    }
    return checked
  })

  if (count !== 0) {
    filterdDatas = [...filterdDatas, { name: '', count }]
  }

  return filterdDatas
}

const filterLanguage = (datas) => {
  let filterdDatas = [...datas]
  let count = 0

  filterdDatas = filterdDatas.filter((data) => {
    let checked = false
    filter.language.map((check) => {
      if (data.name === check) {
        if (data.name.indexOf('( )') !== -1) {
          data.name = data.name.replace('( )', '')
        } else if (data.name.indexOf('') !== -1) {
          data.name = ' '
        }
        checked = true
      }
    })
    if (!checked) {
      count += data.count
    }
    return checked
  })

  if (count !== 0) {
    filterdDatas = [...filterdDatas, { name: '', count }]
  }

  return filterdDatas
}

const filterField = (datas) => {
  let filterdDatas = [...datas]
  let count = 0

  filterdDatas = filterdDatas.filter((data) => {
    let checked = false
    filter.field.map((check) => {
      if (data.name === check) {
        if (data.name.indexOf('(,  )')) {
          data.name = data.name.replace('(,  )', '')
        }
        checked = true
      }
    })
    if (!checked) {
      count += data.count
    }
    return checked
  })

  if (count !== 0) {
    filterdDatas = [...filterdDatas, { name: '', count }]
  }

  return filterdDatas
}

const filterCompany = (datas) => {
  let filterdDatas = [...datas]
  let count = 0

  filterdDatas = filterdDatas.filter((data) => {
    let checked = false
    filter.company.map((check) => {
      if (data.name === check) {
        if (data.name.indexOf('') !== -1) {
          data.name = ' '
        } else if (data.name.indexOf('/LG/SK  ') !== -1) {
          data.name = ''
        }
        checked = true
      }
    })
    if (!checked) {
      count += data.count
    }
    return checked
  })

  if (count !== 0) {
    filterdDatas = [...filterdDatas, { name: '', count }]
  }

  return filterdDatas
}

export default {
  filterMajor,
  filterInfo,
  filterLanguage,
  filterField,
  filterCompany,
}
